import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Vpns from './views/Vpns.vue'
import Pros from './views/Pros.vue'
import Pet from './views/Pet.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/vpns',
      name: 'vpns',
      component: Vpns
    },
    {
      path: '/pros',
      name: 'pros',
      component: Pros
    },
    {
      path: '/pets/:species/:id',
      name: 'pet',
      component: Pet
    }
  ]
})
