<template>
  <div class="home-view-container">
    <h1> SDWAN Traffic Management </h1>
   <h5>Total number of Traffic containers for use: {{ animalsCount }}</h5>
      <h3>
        <font-awesome-icon icon="vpn"/>
        {{ getAllVpns.length }} +
        <font-awesome-icon icon="pro"/>
        {{ getAllPros.length }}
      </h3>
    <button @click="togglePetForm" class="btn btn-primary">Add New Container</button>

    <b-form @submit.prevent="handleSubmit" v-if="showPetForm">
      <b-form-group id="exampleInputGroup2" label="Instance Address:" label-for="exampleInput2">
        <b-form-input
          id="exampleInput2"
          type="text"
          v-model="formData.address"
          required
          placeholder="Enter Address" />
      </b-form-group>

      <b-form-group id="exampleInputGroup3" label="Species:" label-for="exampleInput3">
        <b-form-select id="exampleInput3" :options="['vpns', 'pros']" required v-model="formData.species" />
      </b-form-group>

      <b-form-group id="exampleInputGroup2" label="Instance created" label-for="exampleInput2">
        <b-form-input
          id="exampleInput2"
          type="number"
          v-model="formData.created"
          required
          placeholder="Enter created time" />
      </b-form-group>

      <b-button type="submit" variant="primary">Submit</b-button>
      <b-button type="reset" variant="danger">Reset</b-button>
    </b-form>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'home',
  data() {
    return {
      showPetForm: false,
      formData: {
        adress: '',
        created: 0,
        species: null
      }
    }
  },
  computed: {
    ...mapGetters([
      'animalsCount',
      'getAllVpns',
      'getAllPros'
    ])
  },
  methods: {
    ...mapActions([
      'addPet'
    ]),
    togglePetForm() {
      this.showPetForm = !this.showPetForm
    },
    handleSubmit() {
      const { species, created, address } = this.formData
      const payload = {
        species,
        pet: {
          address,
          created
        }
      }
      this.addPet(payload)

      // reset form after submit
      this.formData = {
        address: '',
        created: 0,
        species: null
      }
    }
  }
}
</script>
