<template>
  <div class="home-view-container">
    <h1>{{ animal.address }} ({{ $route.params.species }})</h1>
    <p>created : {{ animal.created }} time ago</p>
    <p>restarts: {{ animal.restarts }}</p>
    <p>node: {{ animal.node }}</p>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      animal: {}
    }
  },
  computed: {
    ...mapState([
      'vpns',
      'pros'
    ])
  },
  mounted() {
    const animal = this[this.$route.params.species][this.$route.params.id]
    this.animal = animal
  }
}
</script>
